.button {
  background-color: #dba212;
  display: inline-block;
  cursor: pointer;
  border: 1px solid #665528;
  width: 200px;
  margin-top: 5px;
  padding: 12px;
}
.button:hover {
  background-color: #b58f2d;
}
.content{
  display: inline-flex;
  max-width: 75%;
}
.content2{
  display: inline-flex;
}
.content2 div{
  max-width: 20vw;
}
.content2 span{
  max-height: 20px;
}